import React,{useState} from 'react'
import { NavLink ,useHistory} from 'react-router-dom'
import signpic from './images/signupimg.png'

const SignUp = () => {
        const history= useHistory()
        const [user, setuser] = useState({
        name:"", email:"", phone:"", password:"", cpassword:""})

        let name,value;
        const handleInputs =(e)=>{
            console.log(e);
            // name represents diff value like name,email,phone... and value is their value
            name=e.target.name;
            value=e.target.value

            setuser({...user, [name]:value});
        }

        const PostData = async(e)=>{
            e.preventDefault()

            const {name,email,phone,password,cpassword}=user

            const res= await fetch('/register', {
                method: 'POST',
                headers:{
                    "Content-Type": "application/json"
                },
                body:JSON.stringify({
                    name,email,phone,password,cpassword
                })
            })
            const data=await res.json()
            if(res.status===422 || !data ){
                window.alert("Registration failed please try again")
                console.log("Registration failed please try again");

            }
            else{
                window.alert("Registration Successfull")
                console.log("Registration Successfull");
                history.push('/login')
            }
        }
    return (
        <>
            <section className="signup">
            <div className="mt-2">
                <div className="signup-content">
                    <div className="signup-form">
                        <h2 className="form-title">Sign Up</h2>
                        <form method="POST" className="register-form" id="register-form">
                            <div className="form-group">
                                <label htmlFor="name">
                                <i class="zmdi zmdi-account"></i>
                                </label>
                                <input type="text" name="name" id="name" autoComplete="off" 
                                value={user.name}
                                onChange={handleInputs}
                                 placeholder="Enter your name" />
                            </div>
                            <div className="form-group">
                                <label htmlFor="email">
                                <i class="zmdi zmdi-email"></i>
                                </label>
                                <input type="email" name="email" id="email" autoComplete="off" placeholder="Enter your email"
                                value={user.email}
                                onChange={handleInputs} />
                            </div>
                            <div className="form-group">
                                <label htmlFor="phone">
                                <i class="zmdi zmdi-phone-in-talk"></i>
                                </label>
                                <input type="phone" name="phone" id="phone" autoComplete="off"
                                value={user.phone}
                                onChange={handleInputs} placeholder="Enter your phone no" />
                            </div>
                            <div className="form-group">
                                <label htmlFor="password">
                                <i class="zmdi zmdi-lock"></i>
                                </label>
                                <input type="password" name="password" id="password" autoComplete="off" 
                                value={user.password}
                                onChange={handleInputs} placeholder="Enter your password" />
                            </div>
                            <div className="form-group">
                                <label htmlFor="cpassword">
                                <i class="zmdi zmdi-lock"></i>
                                </label>
                                <input type="password" name="cpassword" id="cpassword" autoComplete="off" 
                                value={user.cpassword}
                                onChange={handleInputs} placeholder="confirm your password" />
                            </div>
                            <div className="form-group form-button">
                                <input type="submit" name="signup" id="signup" className="form-submit" value="Register" 
                                onClick={PostData}/>
                            </div>
                        </form>
                    </div>     
                        <div className="signup-image">
                            <figure>
                                <img src={signpic} alt="registration pic" />
                            </figure>
                            <NavLink to="/login" className="signup-image-link">I am already registered</NavLink>
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}

export default SignUp
